.profile__avatar-round {
  grid-area: foto;
}
@media (max-width: 800px) {
	.profile__avatar-round {
    display: block;
    position: relative;
    cursor: pointer;
	}
}

.profile__avatar-round:before {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background: rgba(0, 0, 0, 0.5);
	opacity: 0;
}

.profile__avatar-round:after {
	content: "";
	position: absolute;
	width: 26px;
	height: 26px;
	top: calc(50% - 13px);
	left: calc(50% - 13px);
	background: url(../../../images/updateAvatar.svg);
	background-size: cover;
	background-origin: content-box;
	background-repeat: no-repeat;
	opacity: 0;
}

.profile__avatar-round:hover:before,
.profile__avatar-round:hover:after {
	opacity: 1;
}